<template>
  <div>
    <div class="clearFix">
      <div class="project-tip">
        我的项目
      </div>
      <div class="card-box" v-if="my" v-for="project of my" @click="goProject(team)">
        <Card dis-hover class="card">
          <img :src="baseUrl + project.imgUrl"
               style="position: absolute; top: 0; left: 0; width: 100%; height: 100%;">
          <div style="position: absolute; top: 0; left: 0; width: 100%; height: 100%;">
            <div style="text-align:center;">
              <h3>{{project.name}}</h3>
            </div>
          </div>
          <span href="#" slot="extra" @click.stop="showAddTeam(project)" class="hover">
            <Icon type="android-person-add" class="edit" style="margin-right: 10px;"></Icon>
          </span>
          <span href="#" slot="extra" @click.stop="showEditProject(project)" class="hover">
            <Icon type="edit" class="edit"></Icon>
          </span>
          <span href="#" slot="extra" @click.stop="heartProject(project)" class="heart">
            <Icon type="ios-heart-outline" style=""></Icon>
          </span>
        </Card>
      </div>
      <div class="card-box" @click="editProject = true">
        <Card dis-hover class="card">
          <Icon class="plus" type="plus-round"></Icon>
        </Card>
      </div>
    </div>
    <div class="clearFix">
      <div class="project-tip">
        我参与的项目
      </div>
      <div class="card-box">
        <Card dis-hover class="card">
          <div style="text-align:center">
            <h3>一套基于 Vue.js 的高质量UI组件库</h3>
          </div>
          <a href="#" slot="extra" @click.prevent="changeLimit">
            <Icon type="ios-heart-outline"></Icon>
          </a>
        </Card>
      </div>
    </div>
    <Modal
      :title="formProject.id ? '修改项目' : '创建项目'"
      @on-ok="subEditProject"
      @on-cancel="clearForm"
      v-model="editProject"
      :loading="editProjectLoading"
      class-name="vertical-center-modal">
      <Form ref="formProject" :model="formProject" :rules="ruleProject">
        <div class="ivu-upload" style="width: 250px; height: 100px; margin-bottom: 10px; position: relative;">
          <input type="file" @change="changeImg" class="ivu-upload-input-b">
          <img v-if="formProject.imgUrl" style="width: 250px; height: 100px;" :src="baseUrl + formProject.imgUrl">
          <div class="ivu-upload ivu-upload-drag" v-if="!formProject.imgUrl">
            <div style="padding: 10px 0px;">
              <i class="ivu-icon ivu-icon-ios-cloud-upload"
                 style="font-size: 52px; color: rgb(51, 153, 255);"></i>
              <p>点击上传项目头像</p>
            </div>
          </div>
        </div>
        <Form-item>
          <Radio-group v-model="formProject.isPublic">
            <Radio label="1">公开</Radio>
            <Radio label="2">私有</Radio>
          </Radio-group>
        </Form-item>
        <Form-item prop="name">
          <Input v-model="formProject.name" placeholder="项目名称">
          </Input>
        </Form-item>
        <Form-item>
          <Input v-model="formProject.introduction" type="textarea" placeholder="描述">
          </Input>
        </Form-item>
      </Form>
    </Modal>
    <Modal
      title="用户管理"
      v-model="editTeam"
      class-name="vertical-center-modal">
      <div slot="footer">
        <Button type="primary"
                @click="">
          确认
        </Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import getData from '../config/getData'
  import baseUrl from '../config/baseUrl'
  export default {
    created () {
      this.searchProject()
    },
    data () {
      return {
        baseUrl,
        my: null,
        join: null,
        editProjectLoading: true,
        editProject: false,
        editTeam: true,
        formProject: {
          id: '',
          imgUrl: '',
          name: '',
          isPublic: '1',
          introduction: ''
        },
        ruleProject: {
          name: [
            { required: true, message: '请填写项目名称', trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      changeImg (event) {
        let file = event.target.files[0]
        if (file) {
          let formData = new window.FormData()
          let type = file.name.split('.')
          type = type[type.length - 1]
          formData.append('image', file, file.name)
          formData.append('format', type)
          formData.append('bus', 2)
          getData('fileUpload', formData).then(res => {
            this.formProject.imgUrl = res.data.data.url
          })
        }
      },
      subEditProject () {
        this.$refs.formProject.validate((valid) => {
          if (valid) {
            getData(this.formProject.id ? 'projectUpdate' : 'projectAdd', {
              ...this.formProject,
              isPublic: parseInt(this.formProject.isPublic)
            }).then(() => {
              this.$Message.success(this.formProject.id ? '修改成功' : '创建成功')
              this.clearForm()
              this.editProject = false
              this.searchProject()
            }, () => {
              this.$Message.error(this.formProject.id ? '修改失败' : '创建失败')
              this.editProjectLoading = false
              setTimeout(() => {
                this.editProjectLoading = true
              }, 10)
            })
          } else {
            this.editProjectLoading = false
            setTimeout(() => {
              this.editProjectLoading = true
            }, 10)
          }
        })
      },
      clearForm () {
        this.formProject.id = ''
        this.formProject.imgUrl = ''
        this.formProject.name = ''
        this.formProject.isPublic = 1
        this.formProject.introduction = ''
      },
      searchProject () {
        getData('projectMySearch').then(res => {
          this.my = res.data.data
        }, res => {
        })
        getData('projectJoinSearch').then(res => {
          this.join = res.data.data
        }, res => {
        })
      },
      projectHeart (project) {
        getData('projectHeartAdd', {
          projectId: project.id
        }).then()
      },
      showEditProject (project) {
        this.formProject.id = project.id
        this.formProject.imgUrl = project.imgUrl
        this.formProject.name = project.name
        this.formProject.isPublic = project.isPublic
        this.formProject.introduction = project.introduction
        this.editProject = true
      }
    }
  }
</script>
<style lang="scss" type="text/scss">
  .project-tip {
    margin: 10px 16px 0;
    color: #39f;
    font-size: 16px;
  }
</style>



